.header {
  position: fixed;
  width: inherit;
  z-index: 3;
  height: 93px;
}

.header .container {
  background-color: #84BCB1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  position: relative;
}

.header__link {
  background-color: $logo-color;
  padding: 10px 12px 10px 10px;
  border-radius: 22px;
  display: flex;
  transition: $transition-fast;
}

.header__link:hover {
  background-color: $logo-color-hover;
  transition: $transition-fast;
}

.header__link:active {
  background-color: $logo-color-active;
  transition: $transition-fast;
}

.header__link-text {
  color: $white;
  margin: 0 0 0 10px;
  border-bottom: 1px solid transparent;
}

.header__link-text:hover {
  border-bottom: 1px solid $white;
}

.header__social-networks {
  list-style-type: none;
  padding: 0;
  display: flex;
  margin: 0;
}

.header__social-networks-vk {
  width: 30px;
  height: 30px;
  margin: 0 10px 0 0;
}

.header__social-networks-vk g circle {
  fill: transparent;
}

.header__social-networks-vk g path {
  fill: #fff;
}

.header__social-networks-vk:hover g path {
  fill: hsl(0deg, 0%, 90%);
}

