.steps {
  background-color: hsl(0deg, 0%, 90%);
}

.steps__border-top path {
  fill: rgb(147, 188, 180);
}

.steps .container {
  background-color: rgb(147, 188, 180);
  margin: -15px 0 0 0;
  padding: 0 20px 50px 20px;
}

.steps__title {
  width: 100%;
  line-height: 150%;
  font-size: 62px;
  color: hsl(0deg, 0%, 90%);
  margin: 0;
  text-align: right;
}

.steps__desc {
  color: hsl(0deg, 0%, 90%);
  width: 100%;
  font-size: 20px;
  text-align: right;
}

.steps__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  width: 70%;
  margin: 70px auto 0;
  position: relative;
  background-color: #c7d9c6;
  border-radius: 30px;
  padding: 30px;
}

.steps__item:nth-child(odd) {
  width: 50%;
  margin: 0 50% 0 0;
  padding: 0 15px 150px 0;
  border-right: 4px dashed white;
  position: relative;
}

.steps__item:last-child {
  padding: 0 15px 80px 0;
}

.steps__item:nth-child(odd) .steps__item-count {
  padding: 10px;
  width: 40px;
  height: 42px;
  border-radius: 30px;
  background-color: #fff;
  text-align: center;
  position: absolute;
  top: -28px;
  right: -21px;
}

.steps__item:nth-child(odd) img {
  position: absolute;
  right: -350px;
  top: 0;
}

.steps__item:last-child img {
  position: absolute;
  right: -393px;
  top: 0;
  border-radius: 50px;
}

.steps__item:nth-child(even) .steps__item-count {
  padding: 10px;
  width: 40px;
  height: 42px;
  border-radius: 30px;
  background-color: #fff;
  text-align: center;
  position: absolute;
  top: -32px;
  left: -22px;
}

.steps__item:nth-child(even) {
  width: 50%;
  margin: 0 0 0 calc(50% - 4px);
  padding: 0 0 205px 15px;
  border-left: 4px dashed white;
  position: relative;
}

.steps__item:nth-child(even) img {
  position: absolute;
  left: -393px;
  top: 0;
  border-radius: 50px;
}

.steps__item:nth-child(odd) .steps__item-title,
.steps__item:nth-child(odd) .steps__item-desc {
  width: 100%;
  text-align: right;
}

.steps__item-title {
  font-size: 27px;
  transition: $transition-slow;
}

.steps__item-desc {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0 50px 0 0;
  font-weight: bold;
}

.steps__item:nth-child(even) .steps__item-desc {
  padding: 0 0 0 50px;
}

.steps__item-desc svg {
  position: absolute;
  right: 0;
  top: -10px;
  border-radius: 30px;
  border: 2px solid black;
  padding: 5px;
  margin: 0 0 20px 0;
}

.steps__item-desc span {
  margin: 20px 0 0 0;
  font-style: italic;
  font-weight: normal;
}

.steps__item:nth-child(even) .steps__item-desc svg {
  left: 0;
  border-radius: 15px;
}
